.header-home {
  position: fixed;
  width: 100%;
  display: flex;
  align-items: center;
  background: rgba(35, 35, 35, 0.5);
  height: 39px;
  padding-top: 20px;
  padding-bottom: 20px;
  @media screen and (min-width: 580px) {
    height: 30px;
    padding-top: 20px;
    padding-bottom: 20px;
  }
  @media screen and (min-width: 1100px) {
    height: 40px;
    padding-top: 24px;
    padding-bottom: 24px;
  }
}

.header {
  position: fixed;
  width: 100%;
  display: flex;
  align-items: center;
  background: #fff;
  height: 39px;
  padding-top: 20px;
  padding-bottom: 20px;
  @media screen and (min-width: 580px) {
    height: 30px;
    padding-top: 20px;
    padding-bottom: 20px;
  }
  @media screen and (min-width: 1100px) {
    height: 40px;
    padding-top: 24px;
    padding-bottom: 24px;
  }
}

.container-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 27px;
  @media screen and (min-width: 1100px) {
    padding: 0 27px;
  }
}

.nav {
  width: 100%;
}

.nav_list--close {
  display: none;

  & > button {
    background-color: inherit;
    border: none;
    padding: 10px 8px;
    cursor: pointer;
    margin-left: 10px;
  }
  @media screen and (min-width: 1100px) {
    display: flex;
    align-items: center;

    & > button {
      background-color: inherit;
      border: none;
      padding: 10px 8px;
      cursor: pointer;
    }
  }
}

.nav_list--open {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: absolute;
  top: 100px;
  & > button {
    background-color: inherit;
    border: none;
    padding: 10px 8px;
    cursor: pointer;
    margin-left: 10px;
  }
  @media screen and (min-width: 1100px) {
    display: flex;
    align-items: center;
    flex-direction: row;
    position: static;
    padding-left: 0;

    & > button {
      background-color: inherit;
      border: none;
      padding: 10px 8px;
      cursor: pointer;
    }
  }
}

.nav_link--home {
  position: relative;
  font-family: "Montserrat";
  line-height: 17px;
  font-size: 15px;
  font-weight: 500;
  padding: 10px 16px;
  color: #fff;
  @media screen and (max-width: 1099px) {
    margin-bottom: 25px;
  }

  @media screen and (min-width: 1100px) {
    margin-right: 40px;
  }
}

.nav-link {
  position: relative;
  font-family: "Montserrat";
  line-height: 17px;
  font-size: 15px;
  font-weight: 500;
  padding: 10px 16px;
  color: black;
  @media screen and (max-width: 1099px) {
    margin-bottom: 25px;
  }
  @media screen and (min-width: 1100px) {
    margin-right: 40px;
  }
}

.active {
  font-weight: 700;
  color: inherit;
  &::after {
    display: inline-block;
    content: "";
    width: 81%;
    height: 3px;
    background: #f9820d;
    box-shadow: 0px 4px 10px rgb(212 130 50 / 25%);
    position: absolute;
    bottom: -4px;
    left: 50%;
    transform: translateX(-50%);
  }
}

.active-support {
  color: #cfd8dc;
  background: #eceff1;
  border: 1px solid #cfd8dc;
  cursor: not-allowed;
  box-shadow: none;
  border-radius: 4px;
  text-decoration: none;
  padding: 10px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  width: 121px;
}

.nav_item {
  color: inherit;
}

.nav_item-support {
  text-decoration: none;
  padding: 10px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #ffffff;
  background: rgba(249, 130, 13, 0.7);
  border: 1px solid #f9820d;
  box-shadow: 0px 4px 20px rgb(212 130 50 / 25%);
  border-radius: 4px;
  width: 121px;
}

.phone {
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #f9820d;
}

.nav_change-language--home {
  font-weight: 500;
  font-size: 15px;
  line-height: 17px;
  color: #fff;
}

.nav_change-language {
  font-weight: 500;
  font-size: 15px;
  line-height: 17px;
  color: black;
}

.nav_container-burger-menu {
  display: none;
  @media screen and (max-width: 1099px) {
    display: block;
    width: 18px;
    height: 12px;
    cursor: pointer;
  }
}

.nav_container-list--home {
  @media screen and (min-width: 320px) {
    width: 100%;
    height: 400px;
    position: absolute;
    top: 79px;
    right: 0;
    background: rgba(35, 35, 35, 0.5);
    transition: 0.3s;
    backdrop-filter: blur(20px);
    // animation: modal 0.3s linear forwards;
    transition-timing-function: cubic-bezier(0, 0, 1, 1);
  }
  @media screen and (min-width: 581px) {
    width: 100%;
    position: absolute;
    top: 70px;
    right: 0;
    background: rgba(35, 35, 35, 0.5);
    transition: 0.3s;
    backdrop-filter: blur(20px);
    transition-timing-function: cubic-bezier(0, 0, 1, 1);
  }
  @media screen and (min-width: 1099px) {
    display: none;
  }
}

.nav_container-list {
  @media screen and (min-width: 320px) {
    width: 100%;
    position: absolute;
    top: 79px;
    right: 0;
    background: #fff;
    transition: 0.3s;
    backdrop-filter: blur(20px);
    // animation: modal 0.3s linear forwards;
    transition-timing-function: cubic-bezier(0, 0, 1, 1);
  }
  @media screen and (min-width: 581px) {
    width: 100%;
    // min-height: 100px;
    position: absolute;
    top: 70px;
    right: 0;
    background: #fff;
    transition: 0.3s;
    backdrop-filter: blur(20px);
    transition-timing-function: cubic-bezier(0, 0, 1, 1);
  }
  @media screen and (min-width: 1099px) {
    display: none;
  }
}

